    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.8) 50%,
    rgba(0, 0, 0, 0.9) 100%
  );
  height: 250px;
  padding: 20px;
  border-radius: 3px;
}

#Articles a {
  text-decoration: none;
}

#Articles .foot {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 30px;
}

#Articles .foot p {
  font: 400 10px 'Inter', sans-serif;
  color: var(--secondary-color);
  letter-spacing: 3px;
  text-transform: uppercase;
  text-decoration: none;
}

/* For smaller devices */
@media (max-width: 1150px) {
  #Articles .container {
    display: block;
  }

  #Articles .foot {
    flex-direction: column;
    align-items: flex-end;
  }

  #Articles .foot p {
    margin-bottom: 10px;
  }
}

#Contact {
  align-items: flex-start;
}

#Contact .head {
  margin-bottom: 30px;
}

#Contact .container {
  display: flex;
  flex-wrap: wrap;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  width: 100%;
}

#Contact .content {
  background: var(--background-secondary-color);
  width: 50%;
  padding: 20px;
  border-radius: 3px;
  margin-bottom: 30px;
  flex: 1 0 30%;
}

#Contact .more {
  align-self: flex-end;
}

/* For smaller devices */
@media (max-width: 1150px) {
  #Contact .container {
    flex-direction: column;
  }

  #Contact .content {
    width: 100%;
  }
}

#Foot {
  text-align: right;
  margin-bottom: 60px;
}

#Foot p {
  margin-bottom: 10px;
  line-height: 1.3em;
}

#Foot .links a {
  display: block;
  font: 400 10px 'Inter', sans-serif;
  line-height: 1.8em;
  color: var(--secondary-color);
  letter-spacing: 3px;
  text-transform: uppercase;
  text-decoration: none;
}

#Navbar {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  padding: 0 30px 30px;
  display: none;
}

#Navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.9);
  width: 100%;
  padding: 5px;
  border-radius: 3px;
}

#Navbar .menu {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 3px;
  padding: 5px 20px 18px 20px;
  margin-bottom: 5px;
}

#Navbar .menu-switcher {
  display: flex;
  align-items: center;
  padding-left: 15px;
}

#Navbar .arrow {
  max-height: 5px;
  margin-left: 5px;
}

#Navbar .open {
  -webkit-transform: scaleY(-1);
  transform: scaleY(-1);
}

/* For smaller devices */
@media (max-width: 1150px) {
  #Navbar {
    display: block;
  }
}

:root {
  --glitch-width: 100vw;
  --glitch-height: 100vh;

  --gap-horizontal: 10px;
  --gap-vertical: 5px;

  --time-anim: 0.5s;
  --delay-anim: 2s;

  --blend-mode-1: none;
  --blend-mode-2: none;
  --blend-mode-3: none;
  --blend-mode-4: none;
  --blend-mode-5: overlay;

  --blend-color-1: transparent;
  --blend-color-2: transparent;
  --blend-color-3: transparent;
  --blend-color-4: transparent;
  --blend-color-5: #af4949;

  --background-image: url(https://images.genius.com/78544d5dc557cc9b716e582bc85553eb.1000x1000x1.png);
}

.glitch {
  position: fixed;
  width: 100vw;
  width: var(--glitch-width);
  height: 100vh;
  height: var(--glitch-height);
  filter: grayscale(100%) contrast(400%);
  -webkit-filter: grayscale(100%) contrast(400%);
  -moz-filter: grayscale(100%) contrast(400%);
  overflow: hidden;
  margin: 0 auto;
  opacity: 0.2;
  z-index: -1;
}

.glitch_image {
  position: absolute;
  top: calc(-1 * 5px);
  top: calc(-1 * var(--gap-vertical));
  left: calc(-1 * 10px);
  left: calc(-1 * var(--gap-horizontal));
  width: calc(100% + 10px * 2);
  width: calc(100% + var(--gap-horizontal) * 2);
  height: calc(100% + 5px * 2);
  height: calc(100% + var(--gap-vertical) * 2);
  background: url(https://images.genius.com/78544d5dc557cc9b716e582bc85553eb.1000x1000x1.png);
  background: var(--background-image);
  background-position: bottom;
  background-size: cover;
  background-color: transparent;
  background-color: var(--blend-color-1);
}

.glitch_image:nth-child(2) {
  background-color: transparent;
  background-color: var(--blend-color-2);
  background-blend-mode: none;
  background-blend-mode: var(--blend-mode-2);
}
.glitch_image:nth-child(n + 2) {
  opacity: 0;
}
.glitch_image:hover .glitch_image:nth-child(n + 2) {
  opacity: 1;
}

.glitch .glitch_image:nth-child(2) {
  -webkit-transform: translate3d(10px, 0, 0);
          transform: translate3d(10px, 0, 0);
  -webkit-transform: translate3d(var(--gap-horizontal), 0, 0);
          transform: translate3d(var(--gap-horizontal), 0, 0);
  -webkit-animation: glitch-anim-1-horizontal 0.5s linear infinite;
          animation: glitch-anim-1-horizontal 0.5s linear infinite;
  -webkit-animation: glitch-anim-1-horizontal var(--time-anim) linear infinite;
          animation: glitch-anim-1-horizontal var(--time-anim) linear infinite;
}

.glitch .glitch_image:nth-child(3) {
  -webkit-transform: translate3d(calc(-1 * 10px), 0, 0);
          transform: translate3d(calc(-1 * 10px), 0, 0);
  -webkit-transform: translate3d(calc(-1 * var(--gap-horizontal)), 0, 0);
          transform: translate3d(calc(-1 * var(--gap-horizontal)), 0, 0);
  -webkit-animation: glitch-anim-2-horizontal 0.5s linear infinite;
          animation: glitch-anim-2-horizontal 0.5s linear infinite;
  -webkit-animation: glitch-anim-2-horizontal var(--time-anim) linear infinite;
          animation: glitch-anim-2-horizontal var(--time-anim) linear infinite;
}

.glitch .glitch_image:nth-child(4) {
  -webkit-transform: translate3d(0, calc(-1 * 10px), 0)
    scale3d(-1, -1, -1);
          transform: translate3d(0, calc(-1 * 10px), 0)
    scale3d(-1, -1, -1);
  -webkit-transform: translate3d(0, calc(-1 * var(--gap-horizontal)), 0)
    scale3d(-1, -1, -1);
          transform: translate3d(0, calc(-1 * var(--gap-horizontal)), 0)
    scale3d(-1, -1, -1);
  -webkit-animation: glitch-anim-3-horizontal 0.5s linear infinite;
          animation: glitch-anim-3-horizontal 0.5s linear infinite;
  -webkit-animation: glitch-anim-3-horizontal var(--time-anim) linear infinite;
          animation: glitch-anim-3-horizontal var(--time-anim) linear infinite;
}
.glitch .glitch_image:last-child {
  -webkit-animation: glitch-anim-flash 0.2s steps(2, start) 0s infinite;
          animation: glitch-anim-flash 0.2s steps(2, start) 0s infinite;
}

@-webkit-keyframes glitch-anim-1-horizontal {
  0% {
    opacity: 1;
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
    -webkit-transform: translate3d(var(--gap-horizontal), 0, 0);
            transform: translate3d(var(--gap-horizontal), 0, 0);
    -webkit-clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
    clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
  }
  2% {
    -webkit-clip-path: polygon(0 15%, 100% 15%, 100% 15%, 0 15%);
    clip-path: polygon(0 15%, 100% 15%, 100% 15%, 0 15%);
  }
  4% {
    -webkit-clip-path: polygon(0 10%, 100% 10%, 100% 20%, 0 20%);
    clip-path: polygon(0 10%, 100% 10%, 100% 20%, 0 20%);
  }
  6% {
    -webkit-clip-path: polygon(0 1%, 100% 1%, 100% 2%, 0 2%);
    clip-path: polygon(0 1%, 100% 1%, 100% 2%, 0 2%);
  }
  8% {
    -webkit-clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
    clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
  }
  10% {
    -webkit-clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
    clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
  }
  12% {
    -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
    clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
  }
  14% {
    -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
    clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
  }
  16% {
    -webkit-clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
    clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
  }
  18% {
    -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
    clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
  }
  20% {
    -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
    clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
  }
  21.9% {
    opacity: 1;
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
    -webkit-transform: translate3d(var(--gap-horizontal), 0, 0);
            transform: translate3d(var(--gap-horizontal), 0, 0);
  }
  22%,
