input[type='submit'] {
  -webkit-border-radius: 0px;
  -webkit-appearance: none;
  border-radius: 0;
}

.Doce22 .section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 50px 0;
}

.Doce22 .full-screen {
  height: 100vh;
}

/* General styles */
.Doce22 .title {
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 700;
  font-size: 65px;
  margin-bottom: -2px;
}

.Doce22 .sub-title {
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 2px;
  color: #fff;
}

.Doce22 .description {
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  color: #fff;
}

@media (max-width: 450px) {
  .Doce22 .title {
    font-size: 50px;
    margin-bottom: 0px;
  }

  .Doce22 .sub-title {
    font-size: 14px;
  }

  .Doce22 .description {
    font-size: 12px;
  }
}

/* Content styles */
.section .content .countdown p {
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 700;
  font-size: 50px;
  color: #fff;
  margin: -10px 0;
}

.section .content .countdown h3 {
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
}

/* Head styles */
.Head {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.Head .jvwasquevite {
  max-width: 100px;
  margin-bottom: 30px;
}

.Head .logo h3 {
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 700;
  font-size: 25px;
  margin-bottom: -20px;
}

.Head .logo p {
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  margin-top: -20px;
  color: white;
  opacity: 0.8;
}

/* Glitch text styles */
.glitch-text,
.glitch-text__clip,
.glitch-text__clip::before,
.glitch-text__clip-left {
  position: relative;
  font-family: 'Poppins';
  text-transform: uppercase;
  font-weight: 700;
  font-size: 80px;
  letter-spacing: -3px;
  color: white;
  -webkit-animation: colorSplit 1.25s steps(2, end) infinite;
          animation: colorSplit 1.25s steps(2, end) infinite;
}
.glitch-text__clip,
.glitch-text__clip::before,
.glitch-text__clip-left {
  position: absolute;
  top: 0;
  left: -2px;
  z-index: 10;
  color: #333;
  overflow: visible;
  -webkit-clip-path: polygon(
    0% 0%,
    100% 0,
    100% 25%,
    0 25%,
    0 30%,
    100% 30%,
    100% 50%,
    0 50%,
    0 60%,
    100% 60%,
    100% 65%,
    0 65%,
    0 80%,
    100% 80%,
    100% 85%,
    0 85%,
    0% 0%
  );
          clip-path: polygon(
    0% 0%,
    100% 0,
    100% 25%,
    0 25%,
    0 30%,
    100% 30%,
    100% 50%,
    0 50%,
    0 60%,
    100% 60%,
    100% 65%,
    0 65%,
    0 80%,
    100% 80%,
    100% 85%,
    0 85%,
    0% 0%
  );
  -webkit-animation: glitch_text 1s steps(2, start) infinite;
          animation: glitch_text 1s steps(2, start) infinite;
}
.glitch-text__clip::before {
  content: 'Doce 22';
  left: 0.05em;
  color: white;
  z-index: 9;
  -webkit-clip-path: polygon(
    0% 0%,
    100% 0,
    100% 26%,
    0 26%,
    0 29%,
    100% 29%,
    100% 51%,
    0 51%,
    0 59%,
    100% 59%,
    100% 66%,
    0 66%,
    0 79%,
    100% 79%,
    100% 86%,
    0 86%,
    0% 0%
  );
