/* Foot styles */
.Foot {
  padding-top: 20px;
}

.Foot .social {
  display: flex;
  flex-direction: row;
  padding: 0 20px;
}

.Foot .social .icon {
  margin: 5px;
  border-radius: 3px;
}

.Foot .social img {
  -webkit-filter: invert(100%);
          filter: invert(100%);
  max-width: 18px;
}

.Foot .social p {
  margin-right: 5px;
  margin-top: 3px;
  font-family: 'Poppins';
  font-size: 13px;
  color: white;
}

.Ticket {
  width: 250px;
  border-radius: 3px;
  box-shadow: 0px 4px 0 #9a9d9c;
}

.Ticket .ticket-head {
  background: white;
  text-align: left;
  border-radius: 3px 3px 0 0;
  padding: 15px;
}

.Ticket .ticket-head h3 {
  color: #333;
}

.Ticket .ticket-head span {
  display: block;
  font-family: 'Poppins' !important;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 10px;
  color: #999;
}

.Ticket img {
  max-width: 250px;
  margin-bottom: -5px;
}

.Ticket .ticket-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  padding: 15px 15px 5px 15px;
}

.Ticket .ticket-content .barcode {
  background-image: url(data:image/png;base64,);
  background-repeat: repeat-y;
  width: 85px;
  height: 25px;
}

.Ticket .ticket-content .description {
  font-family: 'Poppins' !important;
  color: #333;
  text-align: right;
}

.Ticket .ticket-content .description span {
  display: block;
  font-size: 10px;
  color: #999;
  margin-bottom: -8px;
}

.Ticket .ticket-form {
  display: flex;
  background: white;
  color: #333;
  padding: 10px;
  border-radius: 0 0 3px 3px;
}

.Ticket .ticket-form form {
  display: flex;
}

.Ticket .ticket-form form input[type='text'] {
  background: #e8e8e8;
  padding: 5px 10px;
  border-radius: 3px 0 0 3px;
  width: 100%;
  border: 0;
}

.Ticket .ticket-form form input[type='submit'] {
  background: #e8e8e8;
  border: 0;
  border-radius: 0px 3px 3px 0px;
  padding: 5px 10px;
  font-weight: 500;
  color: #333;
  cursor: pointer;
}

.Ticket .rip {
