  content: '\2192';
  width: 1em;
  height: 1em;
  text-align: center;
  transition: all 0.35s;
}

.Accordeon .tab-content {
  max-height: 0;
  padding: 0 1em;
  text-align: left;
  transition: all 0.35s;
  font-size: 14px;
}

