:root {
  --background-color: #151515;
  --background-secondary-color: #202022;
  --primary-color: #ffffff;
  --secondary-color: #949495;
  --tertiary-color: #f2c94c;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  background: #151515;
  background: var(--background-color);
  color: #ffffff;
  color: var(--primary-color);
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
}

section,
header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

a {
  color: #ffffff;
  color: var(--primary-color);
  transition: 300ms;
}

a:hover {
  opacity: 0.8;
}

h1,
h2,
h3 {
  font: 500 1em 'Barlow', sans-serif;
  line-height: 1em;
}

h1 {
  font-size: 3em;
  margin-bottom: 10px;
}

h2 {
  font-size: 1.5em;
  margin-bottom: 5px;
}

h3 {
  font-size: 1.1em;
  margin-bottom: 5px;
}

p {
  font: 400 1em 'Inter', sans-serif;
  line-height: 1.6em;
  color: #949495;
  color: var(--secondary-color);
}

p.sub-header {
  font-size: 0.75em;
  text-transform: uppercase;
  color: #f2c94c;
  color: var(--tertiary-color);
  line-height: normal;
  letter-spacing: 3px;
  margin-bottom: 5px;
}

p.secondary-text {
  font-size: 0.9em;
  line-height: normal;
}

button {
  background: #202022;
  width: 70px;
  height: 31px;
  border: none;
  border-radius: 3px;
  font-size: 12px;
  text-transform: uppercase;
  color: #949495;
  color: var(--secondary-color);
  letter-spacing: 3px;
}

.button {
  display: block;
  font: 400 12px 'Inter', sans-serif;
  color: #949495;
  color: var(--secondary-color);
  letter-spacing: 3px;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: 300ms;
}

.button hr {
  display: inline-block;
  width: 30px;
  border: 0;
  height: 0;
  border-top: 1px solid #949495;
  border-top: 1px solid var(--secondary-color);
  margin-right: 10px;
  vertical-align: middle;
  transition: 300ms;
}

.button:hover {
  color: #ffffff;
  color: var(--primary-color);
}

.button:hover hr {
  border-color: #ffffff;
  border-color: var(--primary-color);
  width: 60px;
}

/* For smaller devices */
@media (max-width: 1150px) {
  section {
    height: auto;
    padding: 30px 0;
  }
  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.2em;
  }

  h3 {
    font-size: 1em;
  }

  p {
    font-size: 1em;
  }
}

